.BuildControls {
    width: 100%;
    background-color: #ADEFD1FF;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 1px #ccc;
            box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;

}


.OrderButton {
    background-color: rgb(0, 95, 204);
    outline: none;
    cursor: pointer;
    border: 1px solid rgb(1, 85, 182);
    color: #ffffff;
    font-family: inherit;
    font-size: 1.2em;
    padding: 5px 7px;
    -webkit-box-shadow: 2px 2pnx 2px #181818;
            box-shadow: 2px 2pnx 2px #181818;
    border-radius: 3px;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.OrderButton:hover, .OrderButton:active {
    background-color: rgb(1, 81, 172);
    border: 1px solid rgb(3, 65, 136);
    color: #e0e0e0;
}

.OrderButton:disabled {
    background-color: rgb(226, 226, 226);
    cursor: not-allowed;
    border: 1px solid rgb(190, 187, 187);
    color: #444444;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


